@import '/src/styles/define.scss';

.container {
  width: 100%;
  height: 100%; 
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  z-index: 15;
}


.bkImg{
  background-color: rgba(0, 0, 0, 0.5);
  width: 100vw;
  height: 100vh; 
  z-index: 6;
}

.bk{
  background-color: rgba(255, 255, 255, 0.9);
  width: 100vw;
  height: 100%; 
  border: 0.5px solid rgba(0, 0, 0,1); /* 굵기 2px, 실선, 색상 검정 */
  box-sizing: border-box; //테두리 및 패딩을 포함 크기 계산
  
}
  
.title{
  flex: 1;
  // background-color: #393;
  bottom: 10px;
  height: 100%; 
  .buttonGroup {
    height: 100%; 
    //margin-top: 5em;
    display: flex;
    flex-direction: column;
    // justify-content: center;
    align-items: center;

    // font-family: 'kdg_Medium', sans-serif; // Or similar brush font
    gap: 3em;
    padding: 1em 1.5em;
    
    .customButton {
      // line-height: 3em;
      width: 100%;
      height: 20%;
      // flex: 1;

      background: none;
      font-size: 1.5em;
      font-weight: bold;
      color: #666; // Darker color on hover
      cursor: pointer;
      outline: none;
      white-space: nowrap;
      background-color: rgba(200, 200, 200, 1);
      border: 0.5px solid rgba(145, 136, 57, 0.8); /* 굵기 2px, 실선, 색상 검정 */
      border-radius: 25px;
    }
    
    .selected{
      border: 0.5px solid rgba(145, 136, 57, 0.8); /* 굵기 2px, 실선, 색상 검정 */
      background-color: rgba(255, 200, 200, 1);
      outline: none;
      font-size: 1.5em; // Adjust size as needed
      color: #000; // Darker color on hover
      font-weight: bold;
    }
  
    .separator {
      color: #000; // Gray color for separator
      padding-left: 10px;
      padding-right: 10px;
      font-size: 1.8em; // Match button size
    }
  }
  
}


@keyframes fadeInScale {
  0% {
    opacity: 0;
    transform: scale(0.8); /* 작게 시작 */
  }
  25% {
    opacity: 0.5;
    transform: scale(1.7); /* 살짝 커졌다가 */
  }
  50% {
    opacity: 1;
    transform: scale(1); /* 원래 크기로 */
  }
  75% {
    opacity: 0.5;
    transform: scale(0.8); /* 살짝 커졌다가 */
  }
  100% {
    opacity: 0;
    transform: scale(0.5); /* 작게 시작 */
  }
}

