@import '/src/styles/define.scss';

// myStyle.module.scss
$box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);

.spinner {
  margin-right: 10px;
  display: inline-block;
  width: 16px;
  height: 16px;
  border: 2px solid red;
  border-top: 2px solid transparent;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.bankAccount {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 90%;
  padding: 1em 1.5em;
  gap: 2.2vh;
  @media (min-width: $breakpoint-desktop) {
    width: 70%;
    padding: 1em 15%;
  }
  padding-bottom: 10%;

  .item1{
    flex: 6;
    display: flex;
    flex-direction: row;
    height: 100%;
    width: 100%;
    justify-content: space-between;
    gap: 2.5em;

    .left, .right {
      flex: 5;
      display: flex; // 추가
      justify-content: center; // 가로 가운데 정렬
      align-items: center; // 세로 가운데 정렬
      width: 100%;

    }
    
    .left {
      .card{
        background-color: rgba(160, 169, 245 , 0.3); // right의 색상
      }
    }

    .right {
      .card{
        background-color: rgba(241, 158, 241, 0.3); // right의 색상
      }
    }
    
  }
  
  .item2{
    height: 15%;
    display: flex;
    justify-content: center; // 가로 가운데 정렬
    align-items: center; // 세로 가운데 정렬
    // background-color: #00a;
    width: 100%;
    .card{
      background-color: rgba(167, 238, 153, 0.5); // right의 색상
    }

  }


  
}

.card {
  display: flex;
  text-align: center; // 텍스트 가운데 정렬
  // background-color: #444;
  // padding: 0.8em 0.5em;
  border-radius: 12px;
  box-shadow: $box-shadow;
  height: 100%;
  width: 100%;
 
  align-items: center;
  justify-content: center;

  .accountItem {
    flex: 1;
    width: 100%;
    display: flex;
    flex-direction: column;
    line-height: normal;

    .title{
      flex: 1;
      font-size: 1.5em;
      font-weight: bold;
      .bank{
        font-size: 0.7em;
      }
    }

    .account{
      flex: 1;
      font-size: 1em;
      margin: 0.2em;
    }      

    .copyButton {
      font-family: 'KOTRAHOPE', sans-serif; // Or similar brush fon
      flex: 1;
      padding: 5px 10px;
      background-color: #444;
      color: white;
      border: none;
      border-radius: 4px;
      cursor: pointer;
    }
  }

  .accountButton {
    flex: 1;
    padding: 5px 10px;
    background-color:  rgba(236, 238, 135, 0.9); 
    height: 2.4em;
    font-size: 1.5em;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }

}

.modal {
  position: fixed;
  display: flex;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  flex-direction: column;
  .title {
    flex: 1;
    height: 10%;
    font-size: 1.3em;
    line-height: 1.4em;
    font-weight: bold;
    margin-bottom: 1vh;
  }
  
  .content{
    display: flex;
    flex-grow: 1;
    height: 80%;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 1vh;
  }

  .bottom{
    flex: 1;
    height: 10%;
    margin-bottom: 1vh;
    text-align: right;
  }

}