html, body, .App {
  box-sizing: border-box; //테두리 및 패딩을 포함 크기 계산
  overflow: hidden; /* 스크롤 없애기 */
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-size: clamp(8px, 2vw, 20px);
  line-height: 22px;
  //font-size: 20px;
  // @media (max-width: $breakpoint-mobile) {
  //   font-size: clamp(8px, 2vw, 20px);
  //   // font-size: 12px;
