@import '/src/styles/define.scss';



.main{
  display: flex;
  flex-direction: column;
	height: inherit;
  padding-top: 1em;


  .ckBar{
    position: absolute;
    display: flex;
    flex-direction: row;
    align-content: center;
    //justify-content: space-between;
    gap:.4em;
    align-items: center;
    bottom: 20dvh;
    right: 0;
    z-index: 6;
    background-color: rgba(255, 255, 255, 1);
    color: rgba(0, 0, 0, 0.4);


    // height: 15%;
    border: none;
    font-size: 1.8em;
    cursor: pointer;
    z-index: 9;
    
    .barBt{
      // width: 3em;
      padding: 1em;
      background-color: rgba(0, 0, 0, 0.9);
      color: white;
      display: flex;
      align-items: center; /* 수직 중앙 정렬 */
      justify-content: center; /* 수평 중앙 정렬 */
      writing-mode: vertical-rl; /* 글자를 세로로 배치 */
      // transform: rotate(180deg); /* 글자의 방향을 위에서 아래로 변경 */
    }
    .bar{
      padding:.5em ;
      color: #000;
      flex:5;
      .barTitle{
        color: #000;
        font-size: 1.1em;
        margin-bottom: .7em;
      }

      .form {
        text-align: left;
        display: flex;
        // justify-content: space-between;
        flex-direction: column;
        // gap: 1em;
        .group{
          display: flex;
          justify-content: space-around;

          .inputGroup {
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            gap: 5px;
            
            label {
              font-weight: bold;
              font-size: 0.9em;
              line-height: 1.1em;
              padding-top: 0.4em;
            }
      
            input[type="text"],
            input[type="tel"] {
              padding: 8px;
              font-size: 1em;
              border: 1px solid #ccc;
              border-radius: 4px;
            }
      
            input[type="radio"] {
              margin-right: 0.2em;
            }
  
            .bt2 {
              display: flex;
              background-color: rgba(145, 136, 57, 0.8);
              border: none;
              width: 100%;
              margin: 0em .4em;
              padding: .6em .1em;

              border-radius: 4px;
              font-size: 1em;
              cursor: pointer;
              align-items: center; /* 수직 중앙 정렬 */
              justify-content: center; /* 수평 중앙 정렬 */
              writing-mode: vertical-rl; /* 글자를 세로로 배치 */
              // text-align: center; /* 텍스트 정렬 */
            }
          }
        }
      }
    }
  }

  .ckbarSize{
    // position: absolute;
    // width: 90%;
    bottom: 20dvh;
  }

  .title{
    @media (max-width: $breakpoint-desktop) { display: none;  }
    flex: 1;
    // background-color: #393;
    width: 100%;

    .buttonGroup {
      display: flex;
      justify-content: space-between;
      align-items: center;
      // font-family: 'kdg_Medium', sans-serif; // Or similar brush font
      gap: 1em;
      padding: 0em 1.5em;

      
      .customButton {
        line-height: 2em;
        background: none;
        width: 100%;
        font-size: 1.5em;
        font-weight: bold;
        color: #666; // Darker color on hover
        cursor: pointer;
        outline: none;
        white-space: nowrap;

        border: 0.5px solid rgba(145, 136, 57, 0.8); /* 굵기 2px, 실선, 색상 검정 */
        border-radius: 25px;
      }
      
      .selected{
        border: 0.5px solid rgba(145, 136, 57, 0.8); /* 굵기 2px, 실선, 색상 검정 */
        background-color: rgba(145, 136, 57, 0.5);
        outline: none;
        font-size: 1.5em; // Adjust size as needed
        color: #000; // Darker color on hover
        font-weight: bold;
      }
    
      .separator {
        color: #000; // Gray color for separator
        padding-left: 10px;
        padding-right: 10px;
        font-size: 1.8em; // Match button size
      }
    }
    
  }

  .content{
    flex: 8;
    width: 100%;
    
  }

}
