
$breakpoint-minLimit: 600px;
$breakpoint-mobile: 768px;
$breakpoint-tablet: 1024px;
$breakpoint-desktop: 1200px;
$breakpoint-maxLimit: 1500px;


$breakpoint-mobile-h: 600px;
$breakpoint-tablet-h: 1024px;


@mixin minW($minWidth) {
  @media (min-width: calc($minWidth - 0.1px) ) {
    @content;
  }
}
@mixin maxW($maxWidth) {
  @media (max-width: calc($maxWidth + 0.1px)) {
    @content;
  }
}

//범위
@mixin rangeW($minWidth , $maxWidth) {
  @media (min-width: calc($minWidth - 0.1px) ) and (max-width: calc($maxWidth + 0.1px)) {
    @content;
  }
}
@mixin rangeH($minHeight , $maxHeight) {
  @media (min-height: calc($minHeight - 0.1px) ) and (max-height: calc($maxHeight + 0.1px)) {
    @content;
  }
}

//정확한 사이즈
@mixin exactWH($width , $height) {
  @media  (min-width: calc($width - 0.1px) ) and (max-width: calc($width + 0.1px)) and
		(min-width: calc($width - 0.1px) ) and (max-width: calc($width + 0.1px)) {
    @content;
  }
}
@mixin exactW($width) {
  @media (min-width: calc($width - 0.1px) ) and (max-width: calc($width + 0.1px)) {
    @content;
  }
}
@mixin exactH($height) {
  @media (min-height: calc($height - 0.1px) ) and (max-height: calc($height + 0.1px)) {
    @content;
  }
}



