.backgroundOverlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none; /* 오버레이가 내부 콘텐츠에 영향을 주지 않도록 설정 */
  z-index: -1; /* 콘텐츠 뒤에 배경이 위치하도록 설정 */
}

