@import '/src/styles/define.scss';

@mixin mediaType1() {
	@include rangeW(1360px , 1450px) {
		@include rangeH(900px , 920px){
			@content;
		}
	}
}


.Report {
  width: 100%;
  height: 100%;
  position: relative; // 오버레이 적용을 위해 상대 위치 설정
  border-radius: 10px;
  overflow: hidden; // 자식 요소의 넘침 방지
	color: #000; // 텍스트 색상
	// font-family: '국립박물관문화재단클래식B', sans-serif;
	font-weight: 100;
	font-size: 1.4em;
	letter-spacing:0.035em;

	.title{
    position: relative; // 배경 이미지보다 위에 표시
		z-index: 10; // 텍스트가 오버레이 위에 나타나도록 설정
    color: #000; // 텍스트 색상
		font-weight: bold;
	}

	.textContent {
		position: absolute; // 절대 위치
    left: 50%; // 수평 중앙 정렬을 위해 설정
		transform: translateX(-50%); // 수평 중앙 정렬
		top: 5vh;

		@include minW($breakpoint-desktop){
			top: 30vh;
		}

		width: 100%;
		z-index: 2; // 텍스트가 오버레이 위에 나타나도록 설정
    color: #000; // 텍스트 색상
    font-size: 1.2em; 
		line-height: 1.5em;
    padding-bottom: 20px;
    text-align: center;

		@include mediaType1(){
			top: 10dvh;
			margin-left: 5dvw;
			font-size: 1.1em;
			text-align: left;
		}

		@include exactW(280px) {
			@include exactH(653px){
				font-size: 1.1em;
			}
		}

  }

	.mainContent{
		font-weight: bold;
		// background-color: rgba(145,136,57,.15);
		line-height: 1.6em;
		padding: 0.5em 0.7em;
		border-radius: .5em;
		color: #000;
		font-size: 1.2em;
	}

	.coordinates {
		position: absolute;
		left: 50%; /* 수평 가운데 정렬 */
		transform: translateX(-50%);
		width: 100%;
		bottom: -40dvh;
		// background-color: rgba(145, 136, 57, 0.15);

		@include mediaType1(){
			top: 20dvh;
		}

		@media (min-width: $breakpoint-desktop) {
			bottom: -10dvh;
		}
		
	}


  .point{
    // font-family: 'KOTRAHOPE', sans-serif; 
    font-size: 1.4em;
		color: #f22;
		
  }

	.imgSrc {
		width: 90vw; /* 높이에 맞추기 위해 넓이는 자동으로 설정 */
		height: auto; /* 부모 div의 높이에 맞추기 */
		//top: 5vh; /* 이미지 위로 이동 (필요에 따라 조정 가능) */
		object-fit: cover; /* 이미지가 부모의 비율에 맞게 조정됨 */
		position: relative; /* 위치 조정을 위해 relative 설정 */

		mask-repeat: no-repeat;

		-webkit-mask-repeat: no-repeat;

		mask-image: linear-gradient(to top, 
		rgba(255, 255, 255, 0.15) 10%, 
		rgba(255, 255, 255, 0.4) 30%,
		rgba(255, 255, 255, 1) 50%
		);

		-webkit-mask-image: linear-gradient(to top, 
		rgba(255, 255, 255, 0.3) 10%, 
		rgba(255, 255, 255, 0.4) 30%,
		rgba(255, 255, 255, 1) 50%
		);
		
		/* 미디어 쿼리: 화면 크기가 482px x 640px일 때만 스타일 적용 */
		@include mediaType1(){
			position: absolute;
			padding-left: 30dvw;
			top: 10dvh;
			font-size: 1em;
			text-align: left;
		}

		@media (min-width: $breakpoint-minLimit) {
			top: -5vh; 
		}

		@media (min-width: 10000px) {
			mask-image: linear-gradient(to top, 
			rgba(255, 255, 255, 0.15) 10%, 
			rgba(255, 255, 255, 0.2) 22%,
			rgba(255, 255, 255, 1) 60%
			);
	
			-webkit-mask-image: linear-gradient(to top, 
			rgba(255, 255, 255, 0.15) 10%, 
			rgba(255, 255, 255, 0.2) 22%,
			rgba(255, 255, 255, 1) 60%
			);
		}

		@media (min-width: $breakpoint-mobile) {
			top: -15vh; 
		}
		
		@media (min-width: $breakpoint-tablet) {
			top: -20vh; 
		}
		@media (min-width: $breakpoint-desktop) {
			width: auto; /* 높이에 맞추기 위해 넓이는 자동으로 설정 */
			height: 100vh; /* 부모 div의 높이에 맞추기 */
			mask-image: linear-gradient(to top, 
		rgba(150, 255, 150, 0.15) 10%, 
		rgba(48, 28, 28, 0.5) 40%,
		rgba(255, 255, 255, 1) 60%
		);

		-webkit-mask-image: linear-gradient(to top, 
		rgba(150, 255, 150, 0.15) 10%, 
		rgba(255, 255, 255, 0.5) 40%,
		rgba(255, 255, 255, 1) 60%
		);
		top: -30vh; 
		}
		
		@media (min-width: $breakpoint-maxLimit) {
			top: -30vh; 
		}

	}
	

}
